.modalBackdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.572);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.modalContent {
    background-color: rgb(247, 182, 3);
    width: 20%;
    height: 60%;
    padding: 20px;
    border-radius: 25px;
    box-shadow: 0 2px 4px rgba(0, 0, 0.2, 0.2);
    z-index: 10000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    animation: slideInFromTop 0.5s ease forwards;
}

@keyframes slideInFromTop {
    0% {
        transform: translateY(-100%);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}



/* Анимация для закрытия (снизу вверх) */
@keyframes slideOutToBottom {
    0% {
        transform: translateY(0);
        opacity: 1;
    }

    100% {
        transform: translateY(100%);
        opacity: 0;
    }

}

.imgContent {
    width: 130px;
    height: 130px;
    background-color: purple;
    border-radius: 30px;
    box-shadow: 2px 2px 5px rgb(0, 0, 0);
}

.titleName {
    font-size: 25px;
    font-weight: bold;
    text-transform: uppercase;
    color: purple;
    /* font-family: 'Comfortaa', cursive; */
    font-family: sans-serif;
    text-shadow: 1px 1px 1px rgb(0, 0, 0);
}


.pokemonContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.infoContainer {
    display: grid;
    align-items: flex-start;
    align-content: flex-start;
}

.infoContainer h3 {
    color: rgb(255, 255, 255);
    /* font-family: 'Comfortaa', cursive; */
    font-family: sans-serif;
    font-size: 20px;
    text-shadow: 1px 1px 1px rgb(0, 0, 0);
}

.infoContainer li {
    color: purple;
    font-size: 16px;
    /* font-family: 'Comfortaa', cursive; */
    font-family: sans-serif;
    text-transform: uppercase;
    text-shadow: 1px 1px 1px rgba(255, 0, 170, 0.465);
}

@media (max-width: 769px) {
    .modalBackdrop {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.572);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 9999;
    }

    .modalContent {
        background-color: rgb(247, 182, 3);
        width: 50%;
        height: 50%;
        padding: 70px;
        border-radius: 25px;
        box-shadow: 0 2px 4px rgba(0, 0, 0.2, 0.2);
        z-index: 10000;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .imgContent {
        width: 120px;
        height: 120px;
        background-color: purple;
        border-radius: 30px;
        box-shadow: 2px 2px 5px rgb(0, 0, 0);
    }

    .titleName {
        font-size: 22px;
        font-weight: bold;
        text-transform: uppercase;
        color: purple;
        /* font-family: 'Comfortaa', cursive; */
        font-family: sans-serif;
        text-shadow: 1px 1px 1px rgb(0, 0, 0);
    }

    .infoContainer h3 {
        color: rgb(255, 255, 255);
        /* font-family: 'Comfortaa', cursive; */
        font-family: sans-serif;
        font-size: 18px;
        text-shadow: 1px 1px 1px rgb(0, 0, 0);
    }

    .infoContainer li {
        color: purple;
        font-size: 13px;
        /* font-family: 'Comfortaa', cursive; */
        font-family: sans-serif;
        text-transform: uppercase;
        text-shadow: 1px 1px 1px rgba(255, 0, 170, 0.465);
    }
}